import { Component, Input, ViewChild } from '@angular/core';
import { ChartComponent } from 'ng-apexcharts';

@Component({
  selector: 'app-line-graph',
  templateUrl: './line-graph.component.html',
  styleUrls: ['./line-graph.component.scss']
})
export class LineGraphComponent {
  @Input() title:string = '';
  @Input() xAxisTitle:string = '';
  @Input() yAxisTitle:string = '';
  @Input() type:'bar' | 'donut' = 'bar';
  @Input() pieWidth:number = 390;
  @Input() height:number = 350;
  @Input() colors:string[] | null = [];
  @Input() distributed:boolean = true;
  @Input() stacked = false;
  @Input() legend = true;
  @Input() legendPosition: "top" | "right" | "bottom" | "left" = "bottom";
  @Input() foreColor = '#373d3f';
  @Input() roundYLables:boolean = false;
  @Input() yaxisTimeline:boolean = false;

  
  chartData = [0,0,0,0];
  categories:string[] = [];

  @ViewChild('basicGraph') basicGraph!:ChartComponent;
  options:any = {
    series: [
      {
        name: " ",
        data:this.chartData
      }
    ],
    legend: {
      show:true,
      position:this.legendPosition
    },
    chart: {
      type: "bar",
      foreColor:this.foreColor,
      height: this.height,
      stacked:false,
      zoom: {
        enabled: false
      }
    },
    colors: [
      "#008FFB",
      "#00E396",
      "#FEB019",
      "#FF4560",
      "#775DD0",
      "#546E7A",
      "#26a69a",
      "#D10CE8"
    ],
    plotOptions: {
      bar: {
        columnWidth: "45%",
        distributed: true,
        dataLabels: {
          position: 'top',
          total: {
            style: {
              color:'red'
            }
          }
        }
      },

    },
    dataLabels: {
      enabled: false,
      // offsetY:-10,
      // background: {
      //   enabled: true,
      //   foreColor:'#081a44',
      //   opacity: .3
      // },
      // style: {
      //   fontSize: 10,
      // }
    },
    stroke: {
      curve: "straight"
    },
    title: {
      text: this.title,
      align: "left"
    },
    grid: {
      row: {
        colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
        opacity: 0.5
      }
    },
    xaxis: {
      title: { 
        text: this.xAxisTitle,
        style: {
          fontSize: 14
        }
      },
      categories:this.categories,
      labels: {
        style: {
          fontSize:14
        }
      }
    },
    yaxis: {
      title: {
        text: this.yAxisTitle,
        style: {
          fontSize:14
        }
      }
    },
  };

  optionYaxis:any = {
    labels: {
      formatter: (value:any) => { 
        if(this.roundYLables && value % 1 !== 0) return ''
        else if(this.yaxisTimeline) {
          // Convert seconds to minutes and seconds
          const minutes = Math.floor(value / 60);
          const seconds = value % 60;
          return `${minutes}:${seconds.toString().padStart(2, '0')}`;  // Pads seconds with 0 if needed
        } else 
        return value;
      },
    },
    title: {
      text: this.yAxisTitle,
      style: {
        fontSize:14
      }
    }
  };



  stackedOptions:any = {
    series: [],
    title: { text: this.title },
    chart: {
      type: "bar",
      foreColor:this.foreColor,
      height: this.height,
      stacked: true,
      toolbar: {
        show: true
      },
      zoom: {
        enabled: true
      }
    },
    dataLabels:{
      enabled:false
    },
    plotOptions: {
      bar: {
        horizontal: false
      }
    },
    colors:[
      "#008FFB",
      "#00E396",
      "#FEB019",
      "#FF4560",
      "#775DD0",
      "#546E7A",
      "#26a69a",
      "#D10CE8"
    ],
    xaxis: {
      type: "category",
      categories:[],
      title: {
        text: this.xAxisTitle
      }
    },
    yaxis: {
      title: {
        text: this.yAxisTitle,
        style: {
          fontSize:14
        }
      }
    },
    legend: {
      position: this.legendPosition,
      offsetY: 40
    },
    fill: {
      colors:[],
      opacity: 1

    }
  };


  donutOptions:any = {
    series: [1,1],
    chart: {
      foreColor:this.foreColor,
      width: '100%',
      type: "donut"
    },
    labels: ['a','b'],
    colors: [
      '#FF5733', // Red-Orange
      '#FFC300', // Yellow
      '#F1C40F', // Golden Yellow
      '#DAF7A6', // Light Green
      '#33FF57', // Green
      '#2ECC71', // Emerald
      '#27AE60', // Dark Green
      '#1ABC9C', // Turquoise
      '#3498DB', // Sky Blue
      '#3357FF', // Blue
      '#9B59B6', // Violet
      '#8E44AD', // Dark Purple
      '#581845', // Purple
      '#900C3F', // Maroon
      '#C70039', // Dark Red
      '#E74C3C', // Coral
      '#FF33A6', // Pink
      '#F39C12'  // Orange
    ],
    responsive: [
      {
        breakpoint: 4080,
        options: {
          legend: {
            position: "bottom",
            offsetY: -1,
          }
        }
      },
    ],
    legend: {
      show:true,
      position: "bottom",
      horizontalAlign:'left',
    }
  };

  public reDraw(series:any[], categories:string[]) {
    this.chartData = series;
    this.categories = categories;

    if(this.type === 'bar' && !this.stacked) 
      this.drawBarChart();
    else if (this.type === 'bar' && this.stacked)
      this.drawStackedChart();
    else
      this.drawDonutChart();
  }


  drawBarChart() {
    this.options.xaxis.categories = this.categories;
    this.options.xaxis.title.text = this.xAxisTitle;
    
    this.optionYaxis.title.text = this.yAxisTitle;
    if(this.roundYLables) {
      this.optionYaxis.title.offsetX = 10;
    }

    // this.options.yaxis.title.text = this.yAxisTitle
    this.options.series = this.chartData
    this.options.legend.position = this.legendPosition;
    this.options.plotOptions.bar.distributed = this.distributed;
    this.options.chart.stacked = this.stacked
    this.options.chart.height = this.height;
    this.options.chart.foreColor = this.foreColor;
    
    this.options.legend.show = this.legend
    
    if(Array.isArray(this.colors) && this.colors.length) {
      this.options.colors = this.colors;
    }
    this.options = structuredClone(this.options);

  }


  drawStackedChart() {
    this.stackedOptions.xaxis.categories = this.categories;
    this.options.xaxis.title.text = this.xAxisTitle;
    this.options.yaxis.title.text = this.yAxisTitle
    this.stackedOptions.series = this.chartData
    this.stackedOptions.legend.position = this.legendPosition
    this.stackedOptions.chart.stacked = this.stacked
    this.stackedOptions.chart.foreColor = this.foreColor;
    this.stackedOptions.chart.height = this.height;
    
    if(Array.isArray(this.colors) && this.colors.length) {
      this.stackedOptions.colors = this.colors;
    }

    // Rerender
    this.stackedOptions = structuredClone(this.stackedOptions);
  }


  drawDonutChart() {
    this.donutOptions.labels = this.categories;
    this.donutOptions.series = this.chartData;
    this.donutOptions.legend.position = this.legendPosition;
    this.donutOptions.chart.width = '100%';
    this.donutOptions.chart.height = this.height;
    this.donutOptions.colors = this.colors;
    this.donutOptions.chart.foreColor = this.foreColor;

    // Rerender
    this.donutOptions = structuredClone(this.donutOptions);
  }

  addResponseOption(option:any) {
    if(this.type === 'bar' && !this.stacked) 
      console.log();
    else if (this.type === 'bar' && this.stacked)
      console.log();
    else {
      if(Array.isArray(this.donutOptions.responsive)) {
        this.donutOptions.responsive.push(option);
        this.donutOptions = structuredClone(this.donutOptions);
      }
    }
  }


}
