import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'passwordValidator'
})
export class PasswordValidatorPipe implements PipeTransform {

  transform(value: string | null, ...args: unknown[]): string[] {
    if(!value) return []
    const errors = [];
    if(!value.match(/(?=.*[A-Z])/)) errors.push('UPPERCASE')
    if(!value.match(/(?=.*[a-z])/)) errors.push('LOWERCASE')
    if(!value.match(/(?=.*[0-9])/)) errors.push('NUMBER')
    if(!value.match(/(?=.*[!@#\$%\^&\*])/)) errors.push('SPECIAL')
    if(!value.match(/(?=.{8,})/)) errors.push('MIN')
      
      console.log(errors,'errors');
      
    return errors;
  }

}
